import React from 'react';

const Hub = ({ x, y, active }) => {
    // 
    const sideLength = 15; // 
    // 
    const centerX = x;
    const centerY = y - 10;

    return (
        <rect
            x={centerX}
            y={centerY}
            width={sideLength}
            height={sideLength}
            // SVG  transform 45
            // 
            transform={`rotate(45 ${centerX} ${centerY})`}
            stroke="black"
            strokeWidth="3"
            fill="black"
        />
    );
};

export default Hub;
