import React from 'react';
import { motion } from 'framer-motion';

const Head = ({ totalCost, limitCost, Point, totalNode, stageId }) => {
    const remaining = limitCost - totalCost; // 
    return (
        <>
            <motion.text
                x={20}
                y={30}
                fill="black"
                fontSize="16"
                textAnchor="left"
                style={{
                    pointerEvents: 'none',
                    userSelect: 'none', // 
                }}
            >
                : {stageId + 1}
            </motion.text>
            <motion.text
                x={150}
                y={35}
                fill="black"
                fontSize="24"
                textAnchor="left"
                style={{
                    pointerEvents: 'none',
                    userSelect: 'none', // 
                }}
            >
                :
                <tspan fill={remaining < 0 ? "red" : "blue"}>{remaining}</tspan> {/*  */}
            </motion.text>
            <motion.text
                x={150}
                y={70}
                fill="black"
                fontSize="24"
                textAnchor="left"
                style={{
                    pointerEvents: 'none',
                    userSelect: 'none', // 
                }}
            >
                :
                <tspan fill={Point >= totalNode ? "blue" : "black"}>{Point}/{totalNode}</tspan> {/*  */}
            </motion.text>
        </>
    );


}




export default Head;