import React from 'react';
import { motion } from 'framer-motion';

const Edge = ({ fromNode, toNode, active, cost, onClick }) => {
    const strokeColor = active ? 'black' : '#ddd'; // 
    const hitAreaWidth = '15'; // 

    // 
    const midX = (fromNode.x + toNode.x) / 2;
    const midY = (fromNode.y + toNode.y) / 2;



    return (
        <g onClick={onClick}>
            {/*  */}
            <line
                x1={fromNode.x}
                y1={fromNode.y}
                x2={toNode.x}
                y2={toNode.y}
                stroke={strokeColor}
                strokeWidth="4"
                strokeLinecap="round"
                onClick={onClick}
            />
            {/*  */}
            <line
                x1={fromNode.x}
                y1={fromNode.y}
                x2={toNode.x}
                y2={toNode.y}
                stroke="rgba(0,0,0,0)"
                strokeWidth={hitAreaWidth}
                strokeLinecap="round"
                onClick={onClick}
            />
            {/*  */}
            <motion.circle
                cx={midX}
                cy={midY}
                r="14"
                fill="white"
                stroke="white"
                strokeWidth="1"
                initial={{ scale: 1 }}
                animate={{ scale: 1 }}
                transition={{ duration: 0.2 }}
                style={{
                    pointerEvents: 'none',
                    userSelect: 'none', // 
                }}
                onClick={onClick}
            />
            {/*  */}
            <motion.text
                x={midX}
                y={midY}
                fill="black"
                fontSize="24"
                textAnchor="middle"
                dy=".3em"
                initial={{ opacity: 1 }}
                animate={{ opacity: 1 }}
                transition={{ duration: 0.2 }}
                style={{
                    pointerEvents: 'none',
                    userSelect: 'none', // 
                }}
            >
                {cost}
            </motion.text>
        </g>
    );
}


export default Edge;